import { useEffect, useState } from 'react'

import { useParams } from 'react-router-dom'

import { db } from "@meddbriefer/mdb-firebase"

import { getLog } from '../scripts/connect-firebase'

import { getScenarioByName } from '@meddbriefer/scenario-data'

import Debriefing from '../components/Debriefing'

const Log = () => {

  const { logID } = useParams()
  const [ log, setLog ] = useState(null)
  const [ scenario, setScenario] = useState(null)
  const [loaded, setLoaded] = useState(false)

  useEffect(
    () => {
      getLog(logID)
        .then(logData => {
          setLog(logData)
        })
    }, [logID]
  )

  useEffect(
    () => {
      !!log && getScenarioByName(db, log.scenarioName, "V2")
        .then(scenData => {
          setScenario(scenData)
          console.log(scenData)
        })

    }, [log]
  )

  useEffect(
    () => {
      !!scenario && setLoaded(true)
    }, [scenario]
  )

  if (!loaded) {
    return (<h3>Loading</h3>)
  }
  return ( 
      <Debriefing 
        log={log} 
        scenario={scenario}
      /> 
    )


}

export default Log;
