import React from "react"

import {
    HashRouter as Router,
    Switch,
} from "react-router-dom"

import { AuthProvider, PrivateRoute } from "@meddbriefer/mdb-auth"

import "bootstrap/dist/css/bootstrap.min.css";

import "./styles.css";

import LogList from "./routes/LogList"
import Log from "./routes/Log"


import UnauthedLayout from "./components/UnauthedLayout"

const App = (props) => {

    return (
        <AuthProvider>
            <Router>

                <Switch>
                    <PrivateRoute
                        exact path="/logs/:logID"
                        authFlowLayout={UnauthedLayout}
                        component={Log}
                    />
                    {/*default (catchall) route*/}
                    <PrivateRoute
                        exact match="/"
                        authFlowLayout={UnauthedLayout}
                        component={LogList}
                    />
                </Switch>
            </Router>
        </AuthProvider>
    )
}


export default App;