import { useEffect, useState } from "react"

import { Link } from "react-router-dom"
import { getLogs } from "../scripts/connect-firebase"

import { Table } from "reactstrap"
const LogList = () => {
    const [logs, setLogs] = useState([])
    const [logsLoaded, setLogsLoaded] = useState(false)

    useEffect(
        () => {
            getLogs()
            .then(result => {
                setLogs(result)
                // console.log(result)
                setLogsLoaded(true)
            })
            .catch(error => console.log('error fetching logs', error))
        },
        []
    )

    if (!logsLoaded) {
        return (<h4>fetching log data</h4>)
    }
    return (
        <Table striped={true} borderless size="sm" hover>
            <thead>
                <tr>
                    <th>Label</th>
                    <th>Scenario</th>
                    <th>Username</th>
                    <th>Timestamp</th>
                </tr>
            </thead>
            <tbody>
                {logs.map(log => (
                    <tr key={log.id}>
                        <td><Link to={`/logs/${log.id}`}>{log.label}</Link></td>
                        <td>{log.scenario}</td>
                        <td>{log.userName}</td>
                        <td>{log.timestamp}</td>
                    </tr>
                ))}
            </tbody>
        </Table>
    )

}

export default LogList