import { formatAsMinSecs } from "../utils"

const colorTypes = {
    "Good": "text-success",
    "Caution": "text-warning",
    "Problem": "text-danger",
    "Not implemented": "text-muted"
}

const iconTypes = {
    "Good": "sentiment_satisfied",
    "Caution": "report_problem",
    "Problem": "highlight_off",
    "Not implemented": "sentiment_neutral"
}


export const getAssessmentIcon = (currentEntry) => {
    switch (currentEntry.status) {
        case "contraindicated":
            return iconTypes["Problem"];
        case "missingMinimal":
            return iconTypes["Problem"];
        case "missingRequired":
            return iconTypes["Problem"];
        case "missingStandard":
            return iconTypes["Caution"];
        case "missingAssessment":
            return iconTypes["Problem"];
        case "assessment":
            return iconTypes["Good"];
        case "assessment-option":
            return iconTypes["Not implemented"];
         case "decision-option":
            return iconTypes["Not implemented"];
        case "required-action":
            return iconTypes["Good"];
        case "standard":
            return iconTypes["Good"];
        case "assessment-option-incorrect":
            return iconTypes["Caution"];
        case "decision-option-incorrect":
            return iconTypes["Caution"];
        case "standard-incorrect-answers":
            return iconTypes["Caution"];
        case "minimal":
            return iconTypes["Caution"];
        case "notFound":
            return iconTypes["Not implemented"];
        default:
            return iconTypes["Not implemented"];
    }
}


export const getColor = (currentEntry) => {
    switch (currentEntry.status) {
        case "contraindicated":
            return colorTypes["Problem"];
        case "missingMinimal":
            return colorTypes["Problem"];
        case "missingRequired":
            return colorTypes["Problem"];
        case "missingStandard":
            return colorTypes["Caution"];
        case "missingAssessment":
            return colorTypes["Problem"];
        case "assessment":
            return colorTypes["Good"];
        case "assessment-option":
            return colorTypes["Not implemented"];
        case "decision-option":
            return colorTypes["Not implemented"];           
        case "required-action":
            return colorTypes["Good"];    
        case "standard":
            return colorTypes["Good"];
        case "assessment-option-incorrect":
            return colorTypes["Caution"];
        case "decision-option-incorrect":
            return colorTypes["Caution"];
        case "standard-incorrect-answers":
            return colorTypes["Caution"];
        case "minimal":
            return colorTypes["Caution"];
        case "green":
            return colorTypes["Good"];
        case "yellow":
            return colorTypes["Caution"];
        case "red":
            return colorTypes["Problem"];
        case "notFound":
            return colorTypes["Not implemented"];
        default:
            return colorTypes["Not implemented"];
    }
}


export const getFeedback = (currentEntry) => {
    switch (currentEntry.status) {
        case "contraindicated":
            return currentEntry.contraindicatedWhy;
        case "missingMinimal":
            return currentEntry.minimalWhy;
        case "missingRequired":
            return currentEntry.requiredWhy;
        case "missingStandard":
            return "This is recommended in protocol " + currentEntry.protocol;
        case "missingAssessment":
            return "Did not do during assessment";
        case "assessment":
            return "";
        case "assessment-option":
            return "Not yet checking correctness"; 
        case "decision-option":
            return "Not yet checking correctness"; 
        case "required-action":
            return "";
        case "assessment-option-incorrect":
            return "You chose the wrong assessment option"
        case "decision-option-incorrect":
            return "You chose the wrong decision option"
        case "standard":
            return "";
        case "standard-incorrect-answers":
            return "The intervention is correct but you answered the details incorrectly";
        case "minimal":
            return "You solved the problem but did not follow protocol.  " + currentEntry.minimalWhy;
        case "notFound":
            return "Assessment knowledge for this entry is not specified for this scenario";
        default:
            return "Assessment knowledge for this entry not specified for this scenario";
    }
}


export const getTimestamp = (currentEntry) => {
    if (currentEntry.status === "criticalActionHeader") {
        return "";
    }
    if (currentEntry.timestamp === -1) {
        return "N/A";  // formatAsMinSecs would convert this to 59:59
    }
    return formatAsMinSecs(currentEntry.timestamp);
}
