import React, { useState, useEffect } from 'react';

import { Button, Table } from 'reactstrap';

import { Link } from 'react-router-dom';

import MDBNavBar from "./MDBNavBar"

import { MDB_TYPES } from '@meddbriefer/scenario-data/constants.js';

import { getAssessmentIcon, getColor, getFeedback, getTimestamp } from "./debriefingUtils"

// const Debriefing = ({ log, metaData, criticalActions, associations }) => {
const Debriefing = ({ log, scenario }) => {
    const [showCommentColumn, setShowCommentColumn] = useState(false);
    // eslint-disable-next-line
    const [events, setEvents] = useState(log.events);
    const [formattedActions, setFormattedActions] = useState(null);
    // Set to true once all performed actions have been displayed in the log
    // const [finishedPerformedActions, setFinishedPerformedActions] = useState(false);

    const toggleCommentColumn = () => {
        setShowCommentColumn(!showCommentColumn);
    }

    /* const insertInPhase = (phaseNames, phases, object) => {
        let phase = object.phase;
        let phaseIndex = phaseNames.lastIndexOf(phase);
        let phaseObjects = phases[phaseIndex];
        if (!phaseObjects) { phaseObjects = []; }
        phaseObjects.push(object);
        phases[phaseIndex] = phaseObjects;
    } */

    const insertInPhaseInOrder = (phaseNames, phases, object, objIndex) => {
        let phase = object.phase;
        let phaseIndex = phaseNames.lastIndexOf(phase);
        let phaseObjects = phases[phaseIndex];
        if (!phaseObjects) { phaseObjects = []; }
        phaseObjects.splice(objIndex - 1, 0, object);
        phases[phaseIndex] = phaseObjects;
    }

// this is a hack to approximate a good placement for a missing intervention based on ordering of interventions
// in the required actions definitions (formerly critical actions) that appear in the log and are just before and just after
// the item to be inserted and what is marked as required that is in the log just before and after.  An approach that
// utilizes partial ordering constraints would be better.  Also it is not clear what to do for really badly ordered
// actions on the part of the student.  Note that it only looks for two anchor points before and two anchor points after
// rather than all potential anchor points.  Also does not deal with a phase that is split into multiple within a log
    const insertInPhaseRelativeToSuggestedOrder = (phaseNames, phases, object, orderedArrayObjects, objIndex) => {
        let objectsBefore = orderedArrayObjects.slice(0, objIndex-1)
        let objectBefore = orderedArrayObjects[objIndex-1];
        // console.log("Objects before in def", {...objectsBefore})
        let requiredObjectBefore = objectsBefore.reverse().find(entry => entry.required)
        let objectAfter = orderedArrayObjects[objIndex + 1];
        let objectsAfter = orderedArrayObjects.slice(objIndex + 1)
        let requiredObjectAfter = objectsAfter.find(entry => entry.required)
        let phaseIndex = phaseNames.lastIndexOf(object.phase);
        let phaseObjects = [...phases[phaseIndex]];  //trying for a deep copy of the array mainly for debug via console.log
        let indexBefore = -1
        let indexAfter = -1
        let indexRequiredBefore = -1
        let indexRequiredAfter = -1
        let insertionIndex = false;
        if (objectBefore) { indexBefore = phaseObjects.findIndex(entry => entry.id === objectBefore.id); }
        if (objectAfter) { indexAfter = phaseObjects.findIndex(entry => entry.id === objectAfter.id); }
        if (requiredObjectBefore) { indexRequiredBefore = phaseObjects.findIndex(entry => entry.id === requiredObjectBefore.id); }
        if (requiredObjectAfter) { indexRequiredAfter = phaseObjects.findIndex(entry => entry.id === requiredObjectAfter.id); }
        if (objectAfter) { indexAfter = phaseObjects.findIndex(entry => entry.id === objectAfter.id); }
        /* console.log("Phase Objects", {...phaseObjects})
        console.log("Object to insert", {...object})
        console.log("Object before",{...objectBefore})
        console.log("Object After",{...objectAfter})
        console.log("Required Object Before", {...requiredObjectBefore})
        console.log("Required Object After", {...requiredObjectAfter}) */
        

        // There is a more compact way to do the below but this made
        // it easier for me to get the logic right

        // insert according to required before first as we want to follow something that was required
        if (indexRequiredBefore !== -1){ 
            insertionIndex = indexRequiredBefore + 2;
            // but if the just before object is greater then insert relative to it
            if (indexBefore > indexRequiredBefore){insertionIndex = indexBefore + 2} 
        }
        //if no required before and not required after then insert relative to
        //required item after in critical actions definition
        if (!insertionIndex && indexRequiredAfter !== -1) {
            insertionIndex = indexRequiredAfter + 1
            // unless the just after is smaller then override and insert relative to it
            if (indexAfter < indexRequiredAfter){insertionIndex = indexAfter + 1}
        }
        //otherwide if have a just before insert relative to it
        if (!insertionIndex && indexBefore !== -1) {
            insertionIndex = indexBefore + 2
        }
        //otherwise if have a just after insert relative to it
        if (!insertionIndex && indexAfter !== -1){
            insertionIndex = indexAfter + 1
        }
        //otherwise put at end of phase
        if (!insertionIndex){
            insertionIndex = phaseObjects.length + 2
        }
       insertInPhaseInOrder(phaseNames, phases, object, insertionIndex)
        
    }



    //to display the log using the phase structures accumulated
    const organizeLogDisplay = (phaseNames, phases) => {
        let phaseIndex = -1;
        let displayObjects = [];
        let entryIndex = 1;
        let phaseObjects = [];
        phaseNames.forEach(phase => {
            let criticalActionHeaderColor = "green";
            phaseIndex++;
            phaseObjects = phases[phaseIndex];
            phaseObjects.forEach(phaseObject => {
                if (["contraindicated", "missingAssessment", "missingRequired"].includes(phaseObject.status)) {
                    criticalActionHeaderColor = "red";
                }
                // Red has a higher priority than yellow
                if (["missingStandard", "standard-incorrect.answers"].includes(phaseObject.status) && criticalActionHeaderColor !== "red") {
                    criticalActionHeaderColor = "yellow";
                }
                phaseObject.numericalID = entryIndex;
                entryIndex++;
            })

            let tempHeaderObject = {
                "id": "criticalActionHeader",
                "numericalID": 1,
                "status": criticalActionHeaderColor,
                "contraindicatedWhy": "",
                "minimalWhy": "",
                "requiredWhy": "",
                "label": getPhaseLabel(scenario, phase),
                "protocol": "",
                "actionDescription": "",
                "timestamp": -1
            }
            //console.log(phaseObjects);
            displayObjects.push(tempHeaderObject);
            displayObjects = displayObjects.concat(phaseObjects);
        })
        return displayObjects;
    }

    const evalForIncorrectAnswer = (promptID, answerID) => {
        //check each prompt and if is the appropriate one then  
        let answers = scenario.associations[promptID];

        if (Array.isArray(answers)) {
            //returns bool regarding whether answerID NOT in answers
            return !(answers.find(ansId => ansId === answerID))

        }
        else {
            return !(answers === answerID)
        }
    }

    const getCorrectAnswerLabel = (promptID) => {
        let answers = scenario.associations[promptID]
        let answerLabels = "";
        if (Array.isArray(answers)){
            answers.forEach((answer, j) => {
                scenario.intvMetaData.forEach((entry, i) => {
                    if (entry.id === answer){
                        if (answerLabels === ""){
                            answerLabels = entry.label}
                        else {answerLabels = answerLabels + ", "}
                        return
                        }
                    })
                })
            }

        else {
            scenario.intvMetaData.forEach((entry, i) => {
                if (entry.id === answers){
                    answerLabels = entry.label
                }
            })
        }
        return answerLabels;
    }

    const findHeaderFor = (numericalID, log) => {
        let candidateHeader = {};
        let foundHeader = {};
        for (let entry of log) {
            if (entry.id === "criticalActionHeader") {
                candidateHeader = entry
            }
            else {
                if (entry.numericalID === numericalID) {
                    foundHeader = candidateHeader;
                    break;
                }
            }
        }
        return (foundHeader)
    }

    const getPhaseLabel = (scenario, phaseID) => {
        let data = scenario.phaseData;
        let result = data.find(entry => entry.id === phaseID);
        //console.log(result.label)
        return result.label
    }

    const findRequiredPhases = () => {
        let phases = [];
        scenario.phaseData.forEach((entry, i) => {
            if (entry.type === "phase") {
                phases.push(entry.id)
            }
        })
        return phases
    }


    const processEvents = () => {
        // temporarily make metaData and events directly accessible from the developer console for dev purposes
        // window.metaData = metaData
        window.events = events

        let tempFormattedActions = [];
        let tempConfirmedActions = [];
        //instead of a multi-dimensional array, am using an array of phase names e.g. "BSI", "prinary" and

        let requiredPhaseNames = findRequiredPhases();
        let phaseNames = [];
        let phases = [];     //and another array with the actual content for each phase.  I can search phaseNames for the last
        // phases entry of that name and then use that index to retrieve the appropriate phase entried to
        //add on the missing items (assessment or intervention) for that phase
        let phaseObjects = [];
        let priorPhase = "";
        let phaseIndex = 0;

        // sts 08/13/21 the following block of code (terminated by another comment) could be categorizeAndAnnotateActions()

        // created a formated object for each event
        events.forEach((event, i) => {

            // Each tempObject is one of the lines in the displayed log
            let tempObject = {
                "id": event.id,
                "numericalID": i,  // The number displayed in the "ID" column
                "status": event.type,
                "contraindicatedWhy": "",  // Feedback
                "minimalWhy": "",  // Feedback
                "requiredWhy": "",  // Feedback
                "label": event.label,
                "finding": event.finding,
                "vital": event.vital,
                "value": event.value,
                "displayLabel": "",
                "phase": event.phase,
                "subPhase": event.subPhase,
                "type": event.type,
                "prompt": event.prompt,
                "answerCorrect": true,
                "protocol": "",
                "caLabel": "",
                "intervention": event.intervention,
                "required": false,
                "actionDescription": event.label,  // for the debriefing display
                "timestamp": event.timestamp,
                "processingState": "good"
            }

            //if current phase not the same as prior then keep on with the current phase
            // then save that phase and initialize for a new phase
            if (priorPhase !== event.phase && priorPhase !== "") {
                phases[phaseIndex] = phaseObjects;
                priorPhase = event.phase;
                phaseObjects = [];
                phaseIndex = phaseIndex + 1;
                phaseNames[phaseIndex] = event.phase;
            }
            else {
                if (priorPhase === "") {
                    phaseNames[phaseIndex] = event.phase;
                    priorPhase = event.phase
                }
            }


            let tempType = tempObject.type
            let processedTypes = ["assessment", "required-action", "intervention", "answer", "obtain-vital-sign", "decision-option", "assessment-option"]

            //skip over adding anything not of these types to the debriefing display for now
            if (!processedTypes.includes(tempType)){
                tempObject.processingState = "bad";
            }

            // when get an answer for an intervention, the intervention itself is later in the event log, so 
            // accumulate the answers and then when an intervention is later found we can use the correct label
            // and add the answers to it

            let priortempObject = [];
            if (tempType === "answer") {
                //currently returns correct answer-id if has answered incorrectly or false if answered correctly
                let wrongAnswer = evalForIncorrectAnswer(event.promptID, event.id)
                if (tempConfirmedActions.includes(tempObject.intervention)) {
                    priortempObject = phaseObjects.pop();
                    tempObject.answerCorrect = priortempObject.answerCorrect;
                    if (wrongAnswer) {
                        tempObject.answerCorrect = false;
                        if (priortempObject.actionDescription === "") {
                            tempObject.actionDescription = "should have specified: " + tempObject.prompt + " as: " + getCorrectAnswerLabel(event.promptID);
                        }
                        else { tempObject.actionDescription = priortempObject.actionDescription + ", " + tempObject.prompt + " as: " + getCorrectAnswerLabel(event.promptID)}
                    }
                    else {
                        tempObject.actionDescription = priortempObject.actionDescription;
                        tempObject.answerCorrect = priortempObject.answerCorrect
                    }
                }
                else {
                    if (wrongAnswer) {
                        tempObject.answerCorrect = false
                        tempObject.actionDescription = "should have specified: " + tempObject.prompt + " as: " + getCorrectAnswerLabel(event.promptID);
                    }

                    else { tempObject.actionDescription = "" }
                    tempConfirmedActions.push(tempObject.intervention);
                }
            }

            if (tempType === "intervention") {
                priortempObject = phaseObjects.slice(-1)[0];  //slice returns an array rather than the last object in array

                if (priortempObject && priortempObject.type === "answer" && priortempObject.intervention === tempObject.id) {
                    priortempObject = phaseObjects.pop();
                    tempObject.answerCorrect = priortempObject.answerCorrect;
                    if (priortempObject.actionDescription === "") {
                        tempObject.actionDescription = tempObject.label
                    }
                    else {
                        tempObject.actionDescription = tempObject.label + ", " + priortempObject.actionDescription
                    }
                }
            }

            if (tempObject.processingState === "good") {

                // currently we only need to examine critical actions if the action 
                // is of type intervention

                if (tempObject.type === "intervention") {

                    // iterate through all critical actions looking how to format the
                    // data structue for the current "event"

                    scenario.criticalActions.forEach((ca, j) => {
                        const standards = ca.standard
                        const contrasAll = ca.meta.contraReqOrder.map(contra => contra.id);
                        const contras = ca.contraindicated.map(contra => contra.id);
                        const contraWhys = ca.contraindicatedWhy;
                        let contrasReq = [];
                        let contraIndex = -1;


                        // separate out the required from the plain contraindicated for a critical problem
                        // because we need to handle them differently
                        contrasAll?.forEach((contra, k) => {
                            if (!contras.includes(contra)) {
                                contrasReq.push(contra)
                            }
                        })

                        // sts 08/13/21 I believe we're going through things in the order standard, min, contraindicated is
                        // that there might be multiple matches (I believe all mins are standard too) and we want the last one to win
                        // pwj 08/12/21 yes all minimal steps are assumed to be good and we are assuming
                        // minimal is always a subset of standard



                        // check for this action to be a standard action for a critical problem
                        standards?.forEach((standard, k) => {
                            if (standard.id === tempObject.id) {
                                if (contrasReq.find(entry => entry.id === tempObject.id)) { tempObject.required = true }
                                if (tempObject.answerCorrect) {
                                    tempObject.status = "standard";
                                }
                                else { tempObject.status = "standard-incorrect-answers" }
                                tempObject.label = standard.label;
                                tempObject.protocol = ca.protocol;
                                tempObject.caLabel = ca.label;
                            }
                        })


                        // check for this action to be a "true" contraindication for a critical problem
                        contras?.forEach((contra, j) => {
                            // pwj 9/20/21 here we handle only things that were done that are
                            // contraindicated.  Contraindication due to being required 
                            // need to be handled under missing.  Here we just handle the true 
                            // contraindicated because the action was done and it should not be.  
                            if (contra.id === tempObject.id) {
                                tempObject.status = "contraindicated";
                                //use the contras index so can retrieve the why
                                contraIndex = contrasAll.indexOf(contra.id);
                                tempObject.contraindicatedWhy = contraWhys[contraIndex];
                                tempObject.label = contra.label;
                                tempObject.protocol = ca.protocol;
                                tempObject.caLabel = ca.label;
                            }
                        })

                    })
                    // add the formatted object to our list of formatted objects and the id to our set of confirmed ids
                }
                tempConfirmedActions.push(tempObject.id);
                phaseObjects.push(tempObject);

            }
        })

        // store the last phase processed
        phases[phaseIndex] = phaseObjects;

        // sts 08/13/21 end of categorizeAndAnnotateActions


        // sts 08/13/21 start of categorizeAndAnnotateMissingActions()

        //add missing phase names to phaseNames
        requiredPhaseNames.forEach((phase, i) => {
            if (!phaseNames.includes(phase)) {
                phaseNames.splice(i, 0, phase);  //insert it where it should have appeared
                phases.splice(i, 0, []);  //add the empty array for phase objects
            }
        })


        // check for missing checklist steps
        let indexCounter = events.length
        const checklistSteps = scenario.checkListMetaData.filter(md => [MDB_TYPES.ASSESS, MDB_TYPES.REQUIRED].includes(md.type))
        //console.log("checkliststeps: ", checklistSteps)

        // iterate through all checklist steps for ones for which we don't have formated objects
        checklistSteps.forEach((cls, i) => {
            if (!tempConfirmedActions.includes(cls.id)) {
                // add so we don't check for the missing checklist item again
                tempConfirmedActions.push(cls.id);
                let phaseObject = {
                    "id": cls.id,
                    "numericalID": indexCounter,
                    "type": cls.type,
                    "status": "missingAssessment",
                    "contraindicatedWhy": "",
                    "minimalWhy": "",
                    "label": "Did not do",
                    "finding": "",
                    "phase": cls.phase,
                    "subPhase": cls.subPhase,
                    "caLabel": "",
                    "protocol": "",
                    "actionDescription": cls.label,
                    "timestamp": -1
                };
                insertInPhaseInOrder(phaseNames, phases, phaseObject, i)
                indexCounter++;
            }
        })

        // search for missing interventions for each critical action
        // Doing this separately from the earlier critical actions loop because
        // we want to add in missing assessment steps first

        scenario.criticalActions.forEach(ca => {

            // iterate through all the contra required actions.  These should be in ca standard, but are deemed 
            // more important for "grading purposes"

            // First need to get setup with the right Why information for this critical action
            let contrasReq = [];
            let contraIndex = -1;
            const contrasAllData = ca.meta.contraReqOrder  //this is all the data except which is required and contradicted (i.e. id and label)
            const contrasAll = contrasAllData.map(contra => contra.id);  //get all the ids so can later determine which are required 
            const contras = ca.contraindicated.map(contra => contra.id);  //get the contraindicated ids
            const contraWhys = ca.contraindicatedWhy;
            const standards = ca.standard.map(entry => { const newentry = { "id": entry.id, "label": entry.label }; return newentry })
            //console.log("standards list:", standards);


            // separate out the required from the plain contraindicated because plain are potentially present and required are potentially missing.
            // Note that these are just the ids we're working with here
            contrasAll?.forEach((contra, k) => {
                if (!contras.includes(contra)) {
                    contrasReq.push(contra)
                }
            })

            //if is required add that to annotation to help guide where should insert amongst existing actions
            standards?.forEach(standard => {
                if (contrasReq.includes(standard.id)){standard.required = true;}
            })

            // now we just have to examine standard ids to see what is missing
            standards?.forEach((standard, k) => {
                if (!tempConfirmedActions.includes(standard.id)) {
                    tempConfirmedActions.push(standard.id)
                    let phaseObject = {
                        "id": standard.id,
                        "numericalID": indexCounter,
                        "status": "",
                        "contraindicatedWhy": "",
                        "minimalWhy": "",
                        "requiredWhy": "",
                        "label": standard.label,
                        "finding": "",
                        "phase": ca.when.during.label,
                        "subPhase": standard.subPhase,
                        "type": "intervention",
                        "required": standard.required,
                        "caLabel": ca.label,
                        "protocol": ca.protocol,
                        "actionDescription": standard.label,
                        "timestamp": -1
                    }

                    if (contrasReq.includes(standard.id)) {
                        phaseObject.status = "missingRequired";
                        contraIndex = contrasAll.indexOf(standard.id);
                        phaseObject.requiredWhy = contraWhys[contraIndex].label;
                    }
                    else { phaseObject.status = "missingStandard"; }
                    //standards gives the suggested ordering of interventions for a critical action
                    insertInPhaseRelativeToSuggestedOrder(phaseNames, phases, phaseObject, standards, k);
                    indexCounter++;
                }

            })
        })



        // sts 08/13/21 end of categorizeAndAnnotateMissingActions()


        // sts 08/13/21 start of addHeadersToAnnotatedActions()
        //
        // I think this bit of code could be simplified further by not modifying the original tmpFormattedActions, and simply iterating
        // over it and creating a new data structure, which if the current record has a different label, pushes a header prior to pushing
        // the current record into the new array

        // put the object phases together into one object to represent the annotated log
        tempFormattedActions = organizeLogDisplay(phaseNames, phases);

        // Now insert critical action headers;
        let priorCA = "N/A";

        let finalLog = [];

        tempFormattedActions.forEach((tempObject, j) => {

            if (tempObject.type === "intervention" && tempObject.caLabel && !(priorCA === tempObject.caLabel)) {
                //console.log("prior & current", priorCA, tempObject.caLabel)
                priorCA = tempObject.caLabel;
                let tempHeaderObject = {
                    "id": "criticalActionSubHeader",
                    "numericalID": 1,
                    "status": "green",
                    "contraindicatedWhy": "",
                    "minimalWhy": "",
                    "requiredWhy": "",
                    "label": tempObject.caLabel,
                    "protocol": "",
                    "actionDescription": "",
                    "timestamp": -1
                }
                finalLog.push(tempHeaderObject);
            }
            finalLog.push(tempObject);
        })

        // now we analyze all that we've found in the log to see whether
        // all the standards were done for each critical action and reset
        // the status if not all are there but should be counted
        // as minimal for the feedback.

        // first loop through the formatted actions to collect what was found
        // for each critical action that is standard.  minimal is a subset of standard
        // so only need the standards that were done

        let caActions = [];  //here we store the actual action objects
        let caNames = [];  //here is the critical action names for the above with same order
        let caIndex = 0;
        let caActIndex = -1;

        finalLog.forEach((action, j) => {
            if (action.status.includes("standard", "standard-incorrect-answers")) {  //only interventions have status of standard
                if (!caNames.includes(action.caLabel)) {
                    caActIndex = caIndex;
                    caNames[caActIndex] = action.caLabel;
                    caIndex++;
                }
                else { caActIndex = caNames.indexOf(action.caLabel) }
                let actions = caActions[caActIndex];
                if (!actions) { actions = []; }
                actions.push(action);
                caActions[caActIndex] = actions;
            }
        })


        // If all the minimal were found but not all the standard were, then we
        // need to remark the standard objects created for the minimal events
        // to be minimal instead of standard and to insert the minimalWhy.  It isn't
        // appropriate to give this feedback if all standard are present.
        // Waited until now so that all the events are in one list instead of 
        // a list per phase,

        let processedIds = tempFormattedActions.map(event => event.id);
        let caIds = scenario.criticalActions.map(ca => ca.label);
        // console.log("caIds: ", caIds);
        caNames.forEach((caEntry, j) => {
            //get the critical action definition for this standard entry 
            let caIndex = caIds.indexOf(caEntry);
            let ca = scenario.criticalActions[caIndex];
            let standardNeeded = ca.standard.map(standard => standard.id);
            let minimalNeeded = ca.minimal.map(minimal => minimal.id);
            if (minimalNeeded) {  //no point in doing anything else is no minimals were defined
                let foundActions = caActions[j];
                let found = foundActions.map(ca => ca.id);
                //console.log("sn: ", standardNeeded, "mn: ", minimalNeeded, "found: ", found);
                let allStandardFound = true;
                let allMinimalFound = true;
                for (let item of standardNeeded) {
                    if (!found.includes(item)) {
                        allStandardFound = false;
                        break;
                    }
                }
                for (let item of minimalNeeded) {
                    if (!found.includes(item)) {
                        allMinimalFound = false;
                        break;
                    }
                }
                //console.log("all standard? ", allStandardFound, "all minimal? ", allMinimalFound)   
                let numericalIDsChanged = [];
                if (!allStandardFound && allMinimalFound) {
                    minimalNeeded.forEach((foundId, j) => {
                        let paIndex = processedIds.indexOf(foundId)
                        let object = tempFormattedActions[paIndex];
                        //console.log("object to modify", object);
                        object.minimalWhy = ca.minimalWhy;
                        object.status = "minimal";
                        numericalIDsChanged.push(object.numericalID);
                        tempFormattedActions[paIndex] = object;
                    })
                    //redo color for header if needed
                    let header = {};
                    numericalIDsChanged.forEach((numericalID, z) => {
                        header = findHeaderFor(numericalID, tempFormattedActions);
                        if (header && header.status === "green") { header.status = "yellow" }
                    })

                }
            }

        })


        console.log("Final log:", finalLog);
        setFormattedActions(finalLog);
        // end of addHeadersToAnnotatedActions()


    }


    // eslint-disable-next-line
    useEffect(
        () => {
            processEvents()
            // eslint-disable-next-line
        }, [] // empty list means this code will only be run on initial render
    )



    const dispHeaderRow = (entry) => {
        return (
            <>
                {entry.id === "criticalActionHeader"
                    ? <td colSpan="4" className={getColor(entry)}>
                        {entry.label}
                    </td>
                    : <td colSpan="10" className={"black"} >
                        {entry.label}
                    </td>}
                {showCommentColumn && <td><input /></td>}
            </>
        )
    }

    // all the details to display for a log entry given type and details available
    const getLabel = (entry) => {
        let type = entry.type;
        let subPhase = entry.subPhase;
        let finding = entry.finding;
        let description = entry.actionDescription
        let label = ""
        if (type === "obtain-vital-sign") {
            label = "requested " + entry.vital + ", found = " + entry.value
        }
        else {
            label = type
            if (subPhase) {
                label = label + "/" + subPhase;
            }
            label = label + ": " + description
            if (finding) {
                label = label + ".  Finding: " + finding
            }
        }
        return label

    }

    const dispDataRow = (entry) => {
        return (
            <>
                <td>{entry.numericalID}</td>
                <td>{getTimestamp(entry)}</td>
                <td className={getColor(entry)}>
                    <span className="material-icons-outlined">{getAssessmentIcon(entry)}</span>
                    {getLabel(entry)}
                </td>
                <td>{getFeedback(entry)}</td>
                {showCommentColumn && <td><input id={`comment-${entry.id}`} /></td>}
            </>
        )
    }

    if (null === formattedActions) {
        return (<h3>processing</h3>)
    }
    return (
        <>
            <MDBNavBar
                title={`Document: ${log.label}`}
                leftNav={
                    <Link to="/">Back</Link>
                }
                rightNav={
                    <Button
                        color="primary"
                        onClick={toggleCommentColumn}>
                        {(showCommentColumn) ? "Hide comment column" : "Show comment column"}
                    </Button>
                }
            />
            <form>
                <div style={{ overflowY: "auto", maxHeight: "700px" }}>
                    <Table borderless size="sm">
                        <thead>
                            <tr>
                                <th className="table-header"> ID </th>
                                <th className="table-header"> Timestamp </th>
                                <th className="table-header"> Action Description </th>
                                <th className="table-header"> Feedback </th>
                                {showCommentColumn && <th className="table-header"> Comments </th>}
                            </tr>
                        </thead>
                        <tbody className="scrolling">
                            {formattedActions.map((currentEntry, index) => (
                                <tr key={index}>
                                    {currentEntry.id === "criticalActionHeader" || currentEntry.id === "criticalActionSubHeader"
                                        ? dispHeaderRow(currentEntry)
                                        : dispDataRow(currentEntry)
                                    }
                                </tr>
                            ))}
                        </tbody>
                    </Table>
                </div>
            </form>
        </>
    )
}


export default Debriefing